import { Variants } from "framer-motion"
import React, { useState } from "react"
import DrawerLink from "./DrawerLink/DrawerLink"
import { 
  SDrawer, 
  SDrawerBottomContainer, 
  SDrawerBrandContainer, 
  SDrawerBrandLogo, 
  SDrawerButton, 
  SDrawerButtonImg, 
  SDrawerLinkContainer, 
  SDrawerTopSection, 
  SDrawerUserContainer,  
  SDrawerUserLabels,
  SDrawerUserLinkContainer, 
  SDrawerUserLogo, 
  SDrawerUserMail, 
  SDrawerUserName, 
  SDrawerWrapper
} from "./styled"

const drawerVariants: Variants = {
  open: {
    width: "256px",
  },
  closed: {
    width: "72px"
  }
}

const userLinksVariants: Variants = {
  open: {
    height: "auto"
  },
  closed: {
    height: "0"
  }
}

const links = [
  {
    text: "Dashboard",
    iconSrc: '/dashboard.svg',
    passiveIconSrc: '/dashboardDisabled.svg'
  },
  {
    text: "Settings",
    iconSrc: '/settings.svg',
    passiveIconSrc: '/settingsDisabled.svg'
  }
]

const Drawer = () => {
  const [isOpen, setIsOpen] = useState(true);
  const [userIsOpen, setUserIsOpen] = useState(false);
  const [activeLinkIndex, setActiveLinkIndex] = useState(0);

  const renderLinks = () => {
    return links.map((link, index) => (
      <DrawerLink
        key={link.text}
        iconSrc={index === activeLinkIndex ? link.iconSrc : link.passiveIconSrc} 
        isActive={index === activeLinkIndex}
        onClick={() => setActiveLinkIndex(index)}
      >
        {link.text}
      </DrawerLink>
    ))
  }

  return (
    <SDrawerWrapper>
      <SDrawer 
        animate={isOpen ? "open" : "closed"}
        variants={drawerVariants}
        transition={{
          type: "tween",
          ease: "easeInOut",
          duration: 0.2
        }}
        initial={false}
      >
        <SDrawerTopSection>
          <SDrawerBrandContainer>
            <SDrawerBrandLogo>
              <div>
                <div/>
              </div>
            </SDrawerBrandLogo>
          </SDrawerBrandContainer>
          <SDrawerLinkContainer>
            {renderLinks()}
          </SDrawerLinkContainer>
        </SDrawerTopSection>
        <SDrawerBottomContainer>
          <SDrawerUserLinkContainer
            animate={userIsOpen ? "open" : "closed"}
            variants={userLinksVariants}
            transition={{
              type: "tween",
              ease: "easeInOut",
              duration: 0.2
            }}
            initial={false}
          >
            <DrawerLink
              iconSrc="/logout.svg"
              isActive
            >
              Sign Out
            </DrawerLink>
            <DrawerLink
              iconSrc="/settings.svg"
              isActive
            >
              Profile
            </DrawerLink>
          </SDrawerUserLinkContainer>
          <SDrawerUserContainer
            onClick={() => setUserIsOpen(!userIsOpen)}
          >
            <SDrawerUserLogo>
              JD
            </SDrawerUserLogo>
            <SDrawerUserLabels>
              <SDrawerUserName>
                John Doe
              </SDrawerUserName>
              <SDrawerUserMail>
                john@doe.com
              </SDrawerUserMail>
            </SDrawerUserLabels>
          </SDrawerUserContainer>
        </SDrawerBottomContainer>
      </SDrawer>
      <SDrawerButton
        onClick={() => setIsOpen(!isOpen)}
        tucked={isOpen}
      >
        <SDrawerButtonImg
          flipped={isOpen}
          src="/arrow.svg"
        />
      </SDrawerButton>
    </SDrawerWrapper>
  )
}

export default Drawer