import React, { FC } from "react"
import { SDrawerLink, SDrawerLinkIcon, SDrawerLinkLabel } from "./styled"

const DrawerLink: FC<any> = ({ children, iconSrc, isActive, onClick }) => (
  <SDrawerLink 
    isActive={isActive}
    onClick={onClick}
  >
    <SDrawerLinkIcon>
      <img src={iconSrc} alt="icon"/>
    </SDrawerLinkIcon>
    <SDrawerLinkLabel isActive={isActive}>
      {children}
    </SDrawerLinkLabel>
  </SDrawerLink>
)

export default DrawerLink